.semibtnp{
    margin-top: 5%;
    margin-left: 5%;
    height: 70px;
    width: 90%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .buttonp{
    height: 40px;
    width: 12%;
    border: 2px solid rgba(253, 188, 66, 0.774);
    background-color:rgba(253, 188, 66, 0.774) ;
    color: white;
    font-weight: bold;
    border-radius: 0;
    margin-top: 1%;
    margin-left: 70%;
    text-decoration: none;
    text-align: center;
    align-content: center;
  }
  .buttonp:hover{
    background-color: black;
    border: 2px solid black;
    border-radius: 0;
  }